<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item> <i class="el-icon-menu"></i> 基础表格 </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button type="primary" icon="el-icon-delete" class="handle-del mr10" @click="delAllSelection">批量删除</el-button>
                <el-select v-model="query.address" placeholder="地址" class="handle-select mr10">
                    <el-option key="1" label="广州市" value="选项1"></el-option>
                    <el-option key="2" label="深圳市" value="选项2"></el-option>
                    <el-option key="3" label="北京市" value="选项3"></el-option>
                    <el-option key="4" label="上海市" value="选项4"></el-option>
                </el-select>
                <el-input v-model="query.name" placeholder="用户名" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                :data="tableData.slice((query.pageIndex - 1) * query.pageSize, query.pageIndex * query.pageSize)"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="name" label="用户名"></el-table-column>
                <el-table-column label="账户余额">
                    <template slot-scope="scope">￥{{ scope.row.money }}</template>
                </el-table-column>
                <el-table-column label="头像(查看大图)" align="center">
                    <template slot-scope="scope">
                        <el-image class="table-td-thumb" :src="scope.row.thumb" :preview-src-list="[scope.row.thumb]"></el-image>
                    </template>
                </el-table-column>
                <el-table-column prop="address" label="地址"></el-table-column>
                <el-table-column label="状态" align="center">
                    <template slot-scope="scope">
                        <el-tag :type="scope.row.state === '成功' ? 'success' : scope.row.state === '失败' ? 'danger' : ''">{{
                            scope.row.state
                        }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="date" label="注册时间"></el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="total, prev, pager, next"
                    :current-page="query.pageIndex"
                    :page-size="query.pageSize"
                    :total="pageTotal"
                    @current-change="handlePageChange"
                ></el-pagination>
            </div>
            <!-- 编辑弹出框 -->
            <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
                <el-form ref="form" :model="form" label-width="70px">
                    <el-form-item label="用户名">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="地址">
                        <el-input v-model="form.address"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="editVisible = false">取 消</el-button>
                    <el-button type="primary" @click="saveEdit">确 定</el-button>
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import { fetchData } from '../../../api/index'
export default {
    name: 'BaseTable',
    data() {
        return {
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                pageSize: 4,
            },
            tableData: [],
            currentPage: 1,
            multipleSelection: [],
            delList: [],
            editVisible: false,
            pageTotal: 0,
            form: {},
            idx: -1,
            id: -1,
        }
    },
    created() {
        this.getData()
    },
    methods: {
        getData() {
            fetchData(this.query).then((res) => {
                console.log(res)
                this.tableData = res.list
                this.pageTotal = res.pageTotal || 50
            })
        },
        // 触发搜索按钮
        handleSearch() {
            if (!this.query.name) {
                this.getData()
                return
            }
            let filterData = []
            this.tableData.filter((item) => {
                return item.name.indexOf(this.query.name) ? '' : filterData.push(item)
            })
            this.tableData = filterData
            this.pageTotal = this.tableData.length
            this.$set(this.tableData, 'pageIndex', 1)
        },
        // 删除操作
        handleDelete(index, row) {
            // 二次确认删除
            this.$confirm('确定要删除吗？', '提示', { type: 'warning' })
                .then(() => {
                    this.$message.success('删除成功')
                    this.tableData.splice(index, 1)
                })
                .catch(() => {})
        },
        // 多选操作
        handleSelectionChange(val) {
            this.multipleSelection = val
        },
        delAllSelection() {
            const length = this.multipleSelection.length
            let str = ''
            this.delList = this.delList.concat(this.multipleSelection)
            for (let i = 0; i < length; i++) {
                str += this.multipleSelection[i].name + ' '
            }
            this.$message.error(`删除了${str}`)
            this.multipleSelection = []
        },
        // 编辑操作
        handleEdit(index, row) {
            this.idx = index
            this.form = row
            this.editVisible = true
        },
        // 保存编辑
        saveEdit() {
            this.editVisible = false
            this.$message.success(`修改第 ${this.idx + 1} 行成功`)
            this.$set(this.tableData, this.idx, this.form)
        },
        // 分页导航
        handlePageChange(val) {
            // 加载第几页数据
            this.$set(this.query, 'pageIndex', val)
        },
    },
}
</script>

<style lang="scss" scoped>
.handle-box {
    margin-bottom: 20px;
    .handle-select {
        width: 120px;
    }
    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .mr10 {
        margin-right: 10px;
    }
}

.table {
    width: 100%;
    font-size: 14px;
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
    .red {
        color: #ff0000;
    }
}

.pagination {
    margin: 20px 0;
    text-align: right;
}
</style>
